'use client';

import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import styles from "./game.module.css";

const API_URL = "https://ec2-3-34-134-27.ap-northeast-2.compute.amazonaws.com/api/problems/completed";

interface Post {
  id: number;
  solved: boolean;
  title: string;
  level: string | null;
  correctRate: number | null;
  creator: string;
  type: string | null;
  lastModified: string;
}

const ClientDate = ({ date }: { date: string }) => {
  const [formattedDate, setFormattedDate] = useState("");

  useEffect(() => {
    setFormattedDate(new Date(date).toLocaleDateString());
  }, [date]);

  return <>{formattedDate}</>;
};

const GamePage = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedType, setSelectedType] = useState("전체");
  const [posts, setPosts] = useState<Post[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [currentPage, setCurrentPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [size] = useState(25);

  // 문제 목록 조회만 필요한 경우
  useEffect(() => {
    const fetchPosts = async () => {
      setLoading(true);
      setError("");

      try {
        const membershipId = localStorage.getItem("membershipId"); // membershipId를 로컬 스토리지에서 가져옴
        if (!membershipId) {
          setError("Membership ID not available");
          setLoading(false);
          return;
        }

        const params = new URLSearchParams({
          userId: membershipId, // membershipId 사용
          type: "WARGAME",
          kind: selectedType === "전체" ? "" : selectedType,
          page: currentPage.toString(),
          size: size.toString(),
        });

        const url = `${API_URL}?${params.toString()}`;

        const response = await fetch(url, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
          },
        });

        if (!response.ok) {
          throw new Error("네트워크 응답이 올바르지 않습니다.");
        }

        const data = await response.json();
        setTotalPages(data.totalPages);

        const formattedPosts = data.content.map((post: Post) => ({
          id: post.id,
          solved: post.solved,
          title: post.title,
          level: post.level,
          correctRate: post.correctRate,
          creator: post.creator,
          type: post.type,
          lastModified: post.lastModified,
        }));

        setPosts(formattedPosts);
      } catch (error) {
        setError(error instanceof Error ? error.message : "게시글 로딩 오류");
      } finally {
        setLoading(false);
      }
    };

    fetchPosts();
  }, [currentPage, selectedType, size]);

  const handleSort = (column: string) => {
    // 정렬 기준을 정답율 또는 마지막 수정일로만 제한
    if (column === "correctRate" || column === "lastModified") {
      setSortKind(column);
      setDesc(prevDesc => (prevDesc && column === sortKind) ? !prevDesc : true);
    }
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    console.log("검색어:", searchTerm);
  };

  const goToPage = (page: number) => {
    if (page >= 0 && page < totalPages) {
      setCurrentPage(page);
    }
  };

  const filteredPosts = posts.filter((post) => post.title.includes(searchTerm));

  const getLevelIcon = (level: string | null) => {
    if (!level) {
      return "❓";
    }

    const levelNumber = parseInt(level, 10);
    if (levelNumber === 1) {
      return "⭐";
    } else if (levelNumber === 2) {
      return "⭐⭐";
    } else if (levelNumber === 3) {
      return "⭐⭐⭐";
    }

    return "❓";
  };

  return (
    <section className={styles.game}>
      <div className={styles.pageTitle}>
        <div className={styles.container}>
          <h3>문제</h3>
        </div>
      </div>

      <div className={styles.problemTypeSelector}>
        <div className={styles.container}>
          <label htmlFor="problemType" className={styles.blind}>
            문제 종류 :
          </label>
          <select
            id="problemType"
            value={selectedType}
            onChange={(e) => setSelectedType(e.target.value)}
            className={styles.problemSelector}
          >
            <option value="전체">전체</option>
            <option value="WEBHACKING">webhacking</option>
            <option value="PWNABLE">pwnable</option>
            <option value="REVERSING">reversing</option>
            <option value="CRYPTO">crypto</option>
          </select>
        </div>
      </div>

      <div id={styles.boardList}>
        <div className={styles.container}>
          {loading ? (
            <p className={styles.loadingMessage}>문제를 불러오는 중입니다...</p>
          ) : (
            <table className={styles.boardTable}>
              <thead>
                <tr>
                  <th scope="col" className={styles.thNum}></th>
                  <th scope="col" className={styles.thNum}>
                    문제 번호
                  </th>
                  <th scope="col" className={styles.thDifficulty}>
                    난이도
                  </th>
                  <th scope="col" className={styles.thTitle}>
                    문제 제목
                  </th>
                  <th
                    scope="col"
                    className={styles.thAccuracy}
                    onClick={() => handleSort("correctRate")}
                    style={{ cursor: "pointer" }}
                  >
                    정답율
                  </th>
                  <th
                    scope="col"
                    className={styles.thDate}
                    onClick={() => handleSort("lastModified")}
                    style={{ cursor: "pointer" }}
                  >
                    마지막 수정일
                  </th>
                </tr>
              </thead>
              <tbody>
                {error ? (
                  <tr>
                    <td colSpan={6} className={styles.errorMessage}>
                      {error}
                    </td>
                  </tr>
                ) : filteredPosts.length > 0 ? (
                  filteredPosts.map((post) => (
                    <tr key={post.id}>
                      <td>{post.solved ? "✅" : ""}</td>
                      <td>{post.id}</td>
                      <td>{getLevelIcon(post.level)}</td>
                      <td>
                        <Link href={`/game/${post.id}`}>{post.title}</Link>
                      </td>
                      <td>{post.correctRate !== null ? `${post.correctRate.toFixed(0)}%` : "-"}</td>
                      <td>
                        <ClientDate date={post.lastModified} />
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={6}>문제가 없습니다. ㅠㅠ</td>
                  </tr>
                )}
              </tbody>
            </table>
          )}
        </div>
      </div>

      <div className={styles.pagination}>
        {totalPages > 0 &&
          Array.from({ length: totalPages }, (_, index) => (
            <span
              key={index}
              className={`${styles.pageButton} ${currentPage === index ? styles.active : ""}`}
              onClick={() => goToPage(index)}
            >
              {index + 1}
            </span>
          ))}
      </div>

      <button
        onClick={() => router.push("/game/game_create")}
        className={styles.createButton}
      >
        문제 생성
      </button>
    </section>
  );
};

export default GamePage;
